body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-poppins: Poppins;

  /* font sizes */
  --font-size-xl: 20px;
  --font-size-5xl: 24px;
  --font-size-17xl: 36px;
  --font-size-21xl: 40px;

  /* Colors */
  --color-white: #fff;
  --color-black: #000;
  --color-darkred-100: #8a0606;
  --color-darkred-200: rgba(138, 6, 6, 0.29);
  --color-maroon: #621d1d;
  --color-firebrick: #c90a0a;

  /* border radiuses */
  --br-21xl: 40px;
}
